import {mapState, mapActions, mapMutations} from 'vuex'
import dialogCheck from '../components/dialogCheck'
import {regionData, CodeToText, TextToCode} from 'element-china-area-data'

export default {
  data() {
    return {
      allStaff: [],   //  所有员工
      staff: '',      //转给他人：员工
      formTransfer: {
        checkbox1: false,   //  是否保留为团队成员
        checkbox2: false,   //  是否转移线索
        checkbox3: false,   //  是否转移合同
        checkbox4: false,   //  是否转移机会
      }, //  转给他人checkbox

      data_con:{},//存储对象的容器
      query_show: false,          //  是否显示更多查询
      schecked: true,
      currentPage: 1,//默认开始页面
      pagesize: 50,//每页的数据条数
      activeIndex: '1',   // 当前标签
      dsQuery: {            //  查询条件
        name: '',           //  客户名称
        own: '',            //  拥有者
        type: '',           //  客户类型
        state:'',           //  跟进状态
        phone:'',           //  电话
        classify: '',       //  分类
        follow_time: [],    //  最近跟进时间
        create_time: [],    //  创建时间
      },
      items: [],
      tableData: [],//客户表格数据展示
      jurisdiction: [],//查看权限人员集合
      options: regionData,    //  省市区配置
      selectedOptions: [],
      selectValue: [],//勾选的客户数据
      mergeClient1: {},//合并客户数据1
      mergeClient2: {},//合并客户数据2
      customerField:[],
      customerAdd: {},//新增客户
      customerAddLabel: [],//新增客户label
      // {"label":"主键","readOnly":null,"editorType":null,"value":"0","selectList":null},
      formAdd1: [],
      
      formImport: {
        active: 0,
        type: '不导入',
        fileList: [],
      },
      formCheck: {},
      formMerge: {
        id: ''
      },
      inputStaff: '',    // 团队人员变更输入员工姓名或工号
      dialogAddVisible: false,  // 新增
      dialogImportVisible: false,  // 导入
      dialogMergeVisible: false,  // 合并
      dialogTransferVisible: false,  // 转给其他人
      dialogChangeVisible: false,  // 团员变更
      dialogCheckVisible: false,  // 查看公司信息
      customer_rules:{},//添加客户校验规则
      restaurants: [],//下拉框员工的查询集合
      

      select_con:[],

      importFile: '',     //   要导入的文件
      
      khlx: [],         //  客户类型
      gjzt: [],         //  跟进状态

      ab: {
      },
      
    };
  },
  components: {
    dialogCheck,
  },

  mounted() {
    //  获取数据字典
    let that = this
    let list = ["crm-khlx", "crm-kh-gjzt"]
    this.$post("/crm/getDict", {"list": list}, (data) => {
      this.khlx = data['crm-khlx']
      this.gjzt = data['crm-kh-gjzt']
    })
  },
  activated() {

    /**
     * 页面加载前获取所有客户数据
     */
    this.getCustomers();
  },


  methods: {
    //  获取客户列表
    getCustomers(){
      this.$post("/crm/customerPR/queryCrmCustomer",this.dsQuery, (data) => {
        this.tableData = data.list;
      })
    },

    code_to_text(arr){
      let area = '';
      if(arr){
        arr.map((code) => {
          area += CodeToText[code]
        })
      }
      return area
    },


/*--------------------------------------------转给他人功能--------------------------------------------*/
    // 打开转给他人弹框
    turnOther(){
      if (this.selectValue.length < 1) {
        this.$message.error('请勾选数据！');
        return;
      }
      //  如果没有获取所有员工请求员工数据
      if(!this.allStaff.length){
        this.$post("/crm/getAllStaff", {}, (data) => {
          this.allStaff = data.list
        })
      }
      this.dialogTransferVisible = true
    },
    // 转给他人确定按钮
    turn_other_sava(){
      if(!this.staff){
        this.$message.error("请选择新拥有者！")
        return
      }
      let list = []
      this.selectValue.map(item => {
        list.push(item.id)
      })
      let params = {
        list: list,
        ownerId: this.staff,
        ifLeft: this.formTransfer.checkbox1
      }
      this.$post("/crm/customerPR/turnOther", params, (data) => {
        this.dialogTransferVisible = false
        this.$message.success("转移成功！")
        this.getCustomers()
        this.$refs.table.clearSelection()
      })
    },

    




    /**
     * 关闭弹框清空表单
     */
    dlgClose(form_name){
      this.$refs[form_name].resetFields();
    },

    // 分页
    handleCurrentChange(val){
      this.currentPage = val;
    },

    // 新增按钮
    closeAdd(){
      this.$refs['customerAdd'].resetFields();
    },
    checkPhone(rule, value, callback){
      //  手机号和电话验证    区号-电话号码 /电话号码/手机号
      let reg = /(^(\d{3,4}-)?\d{7,8})$|(1[0-9]{10})/;
      if(reg.test(value)){
        callback()
      }else{
        callback(new Error('电话号码错误!'))
      }
    },
    addCrmCustomer() {
      this.$post("/crm/extFieldPR/getField",{tableName: 'crm_customer'}, (data) => {
        this.customerAddLabel = data.list;
        console.log(this.customerAddLabel)
        data.list.map((item) => {
          if (item.editorType != 'date'){
            // this.customerAdd[item.name] = item.value
            if (item.editorType == 'cascade' & !item.value){
              this.customerAdd[item.name] = []
            }
          }
        })
        this.customer_rules['name'] = { required: true, message: '请输入客户名称', trigger: 'blur' }
        this.customer_rules['phone'] = { required: true, validator: this.checkPhone, trigger: 'blur' }
        this.dialogAddVisible = true;
      })
    },

    /**
     * 新增客户数据保存确认按钮
     */
    customerSava(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$post("/crm/customerPR/addCrmCustomer",this.customerAdd, (data) => {
            this.$message.success('操作成功！');
            this.getCustomers();
            this.dialogAddVisible = false;
          })
        }
      });
    },

    // 删除客户
    deleteCrmCustomer(){
      var select_value = this.selectValue;
      if (select_value.length < 1) {
        this.$notify.warning({ message: '请勾选数据！'});
        return;
      };

      this._confirm('确定删除吗?', () => {
        let list = []
        this.selectValue.map(item => {
          list.push(item.id)
        })
        this.$post('/crm/customerPR/deleteCrmCustomer', {"list": list}, (data) => {
          if(data.message){
            this.$message.error(data.message)
          }else{
            this.$message.success("删除成功！")
          }
          this.$refs.table.clearSelection()
          this.getCustomers();
        })
      })
    },

    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },

    handleChange(value) {
      console.log(value)
    },

    onSubmit(){},

    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    // 排序
    sortChange(column, prop, order){
      
    },
    /**
     * 勾选的数据
     */
    handleSelectionChange(val) {
      this.selectValue = val;
    },

    closeCheckDialog() {
      // this.dialogCheckVisible = false;
      console.log(1);
    },
    closeAddDialog() {
      // this.dialogCheckVisible = false;
      console.log(1);
    },


    /**
     * 打开合并客户模态框
     */
    mergeClient() {
      if(this.selectValue.length == 2){
        this.mergeClient1 = this.selectValue[0]
        this.mergeClient2 = this.selectValue[1]
        this.chooseMianMerge('mergeClient1', 'mergeClient2')
        this.dialogMergeVisible = true
      }else{
        this.$alert('请选择2个客户', '提示', {
          confirmButtonText: '确定',
          callback: action => {}
        });
      }
    },

    // 选择主记录
    chooseMianMerge(merge1,merge2){
      this.formMerge = this.deepClone(this[merge1]);
      this[merge1].area = this.formMerge.area
      this.data_con = this[merge2].id
    },
    // 合并客户
    mergeSubmit() {
      var param = new Object();
      param['data'] = JSON.stringify(this.formMerge);
      param['id'] = this.data_con;
      console.log(this.formMerge)
      this.$post("/crm/crmCustomerPR/mergeCustomer",param, (data) => {
        this.$message.success('操作成功！');
        this.dialogMergeVisible = false;
        this.getCustomers();
      })

    },

    /**
     * 查看客户
     */
    handleCheck(data) {
      console.log(data);
      this.formCheck = data
      this.dialogCheckVisible = true
    },

    /**
     * 查看客户详情
     */
    handleCheckInfo(id) {
      this.$router.push({
        name:'client_detail',
        query:{
          id: id
        }
      });
    },



    /**
     * 添加成员
     */
    add_jurisdiction(){
      var data = new Object();
      //模块
      data['module'] = this.$store.state.mode_name.crm_customer;
      //输入框的值
      data['userIdOrPhone'] = this.inputStaff;
      var url = this.$store.state.crmPath+'/crm/staffManagePR/queryStaffForCrm';
      this.$axios.post(url, data).then(res => {
        var r = res.data;
        this.$notify({ message: r.tip, position: 'bottom-right'});
        this.init_jurisdiction();
      });
    },

    /**
     * 修改人员权限
     */
    update_jurisdiction(){
      var data = new Object();
      //模块
      data['data'] = JSON.stringify(this.jurisdiction);
      var url = this.$store.state.crmPath+'/crm/crmJurisdictionPR/updateJurisdiction';
      this.$axios.post(url, data).then(res => {
        this.dialogChangeVisible = false
        this.$notify({ message: "操作成功！", position: 'bottom-right'});
      });


    },

    /**
     * 删除权限
     */
    deleteRow(obj) {

      this.$confirm('确定删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        var data = new Object();
        //模块
        data['id'] = obj.id;
        var url = this.$store.state.crmPath+'/crm/crmJurisdictionPR/deleteJurisdiction';
        this.$axios.post(url, data).then(res => {
          this.init_jurisdiction();
          this.$notify({ message: "操作成功！", position: 'bottom-right'});
        });
      });



    },

    renderHeader(createElement) {
      return createElement(
        'el-popover',
        {
          attrs: {'trigger': 'hover', 'placement': 'top'}
        },
        [
          createElement(
            'p',
            {
              attrs: {type: 'text'}
            },
            'asgfasgas'
          ),
          createElement(
            'i',
            {
              'class': 'iconfont icon-guanliandingdan',
              'slot': 'reference'
            }
          )
        ]
      );

    },

    //  上传文件
    upload(){
      document.getElementsByClassName('upload-input')[0].click();
    },
    chooseFile(e){
      this.importFile = e.target.files[0]
    },
    uploadSubmit(){
      let params = new FormData();
      params.append('file', this.importFile)
      if(this.importFile){
        this.$axios.post(this.$store.state.crmPath+"/crm/crmCustomerPR/uploadCustomer", params).then(res => {
            console.log(res.data);
            this.dialogImportVisible = false
        }).catch(error => {
            console.log('error')
        })
      }else{
        this.$alert('请选择要导入的文件', '提示', {
          confirmButtonText: '确定',
          callback: action => {}
        });
      }
    },
  }
}



